* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  font-size: 13px;
  background-color: #f2f2f2;
}

.content {
  max-width: 960px;
  margin: 1em auto;
  height: 400px;
  padding: 1em;
  animation: fadein 0.3s ease-out 0.3s;
  animation-fill-mode: both;
}

.sites-row {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

.site {
  /*flex-grow: 1;*/
  flex-shrink: 1;
  width: calc(33% - 10px);
  min-width: 200px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background: #fff;
  border-radius: 2px;
  margin: 10px 10px 10px 0;
  padding: 1em;
  position: relative;
  transition: all 0.1s ease-out;
}

.site.site-removal-intended {
  opacity: 0.3;
}

.site-link {
  color: rgba(0, 0, 0, 0.54);
}

.site-link:hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.27), 0 3px 1px -2px rgba(0, 0, 0, 0.4),
    0 1px 5px 0 rgba(0, 0, 0, 0.24);
}

.site-link:active {
  background: #f2f2f2;
  transform: scale(0.99);
}

.site-favicon {
  display: inline-block;
  width: 16px;
  margin-right: 5px;
}

.site-title {
  display: inline-block;
  width: calc(100% - 21px);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.site-remove {
  position: absolute;
  top: 0;
  right: 5px;
  font-size: 18px;
}
.site-remove:after {
  content: "\00D7";
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
